import Router from 'vue-router'
const routes = [{
  path: '/dribbble-vue/dist',
  redirect: '/'
}, {
  path: '/',
  component: resolve => require(['../views/index.vue'], resolve)
}]

const router = new Router({
  routes,
  mode: 'history'
})

export default router



